* {
    margin: 0;
    padding: 0;
  }
  /* App.js */
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    min-height: 100vh;
    background-color: #425f9c;
    /* background: linear-gradient(to right bottom, #f8edeb, #fdf4ec); */
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .app-wrapper {
    background-color: #ffffff;
    min-width: 450px;
    min-height: 650px;
    padding: 30px;
    box-sizing: border-box;
    border-radius: 10px;
    box-shadow: 3px 6px 40px #425f9c;
    margin-bottom: 10px;
  }
  .title h1 {
    color: #e90d0d;
    text-align: center;
    margin: 30px 0;
  }
  .task-input {
    outline: none;
    width: 260px;
    padding: 15px;
    margin-right: 25px;
    font-size: 20px;
    color: #050505d3;
    background-color: #ced1d8;
    border: 1px solid #92989e;
    border-radius: 10px;
  }
  .button-add {
    width: 80px;
    padding: 10px 10px;
    font-size: 14px;
    border-radius: 10px;
    border: 0;
    background-color: #f18673;
    cursor: pointer;
  }
  .list-item {
    display: flex;
    margin: 20px 0;
    border: 1px solid #fffffc;
    border-radius: 10px;
    padding: 10px;
    max-height: 30px;
    border: 1px solid #e21515;
  }
  .button-complete,
  .button-delete,
  .button-edit {
    border: none;
    outline: none;
    cursor: pointer;
    background-color: transparent;
    font-size: 30px;
    border-radius: 50%;
  }
  .button-complete {
    color: #7173e9;
    margin-right: 10px;
  }
  .button-edit {
    color: #ddc918;
    margin-right: 10px;
  }
  .button-delete {
    color: #e72727;
  }
  .complete {
    text-decoration-style: solid;
    text-decoration-line: line-through;
    text-decoration-color: #fffffc;
    opacity: 0.6;
  
    display: flex;
    margin: 20px 0;
    border: 1px solid #e21515;
    border-radius: 10px;
    padding: 10px;
    max-height: 30px;
  }
  .list {
    width: 220px;
    background-color: #ff0000;
    border: none;
    color: #fff;
    font-size: 20px;
    padding-left: 10px;
    margin-right: 15px;
  }
  .list:focus {
    background-color: #4bd414;
    border-radius: 10px;
  }
  input:focus,
  button:focus {
    outline: none;
  }